<template>
	<div>
		<h4>Cuestionarios</h4>

		<hr>

		<p>
			<button @click="agregarCuestionario">Nuevo</button>
		</p>

		<table border="1">
			<thead>
				<tr>
					<th>
						Nombre Formulario
					</th>
					<th>
						Operaciones
					</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="cuestionario in cuestionarios">
					<td>{{ cuestionario. nombre }}</td>
					<td>
						<button @click="editar(cuestionario.id)">Edicion</button>
						<button @click="ver(cuestionario.id)">Resultado</button>
					</td>
				</tr>
			</tbody>
		</table>

	</div>
</template>

<script>
	export default {
		name: 'app',
		data () {
			return {
				fields: [
					{ key: 'nombre', label: 'Nombre' },
					{ key: 'acciones', label: 'Acciones' }
				],
				cuestionarios: []
			}
		},
		methods: {
			editar (id) {
				this.$router.push(`/editar/${id}`)
			},
			ver (id) {
				this.$router.push(`/ver/${id}`)
			},
			agregarCuestionario () {
				this.$db.cuestionarios
					.add({
						nombre: '',
						preguntas: []
					})
					.then((id) => {
						this.$router.push(`/editar/${id}`)
					})
					.catch(() => {
						alert('Error al agregar un cuestionario')
					})
			},
			fetchData () {

				this.$db.cuestionarios
					.toArray()
					.then((cuestionarios) => {
						this.cuestionarios = cuestionarios
					})
			}
		},
		created () {
			this.fetchData()
		}

	}
</script>
<style scoped>

</style>



